.typewriter {
  font-weight: bold;
  font-size: 2em;
  position: relative;
}
.scrollDownLeft {
  position: absolute;
  left: 0;
  writing-mode: vertical-rl;
  text-orientation: mixed;
  color: grey;
  animation-name: scrollBoing;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}
.scrollDownRight {
  position: absolute;
  right: 0;
  writing-mode: vertical-rl;
  text-orientation: mixed;
  color: grey;
  animation-name: scrollBoing;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes scrollBoing {
  0%   { bottom:0px;}
  50%  {bottom: 10px;}
  100% {bottom:0px}
}

@keyframes scrollBoing {
  0%   { bottom:0px;}
  50%  {bottom: 10px;}
  100% {bottom:0px}
}

@keyframes scrollLineAppear {
